/** @jsxImportSource @emotion/react */
import ReactPlayer from 'react-player';
import { css } from "@emotion/react";

export function YoutubeSlide (
    {   
        YoutubeData1,
        YoutubeData2,
        //YoutubeData3,
    }){
    return (
        <div className='YoutubeSlide'>
            <h2
                css={css`
                text-align: center;
                padding-top: 10px;
                padding-bottom: 10px;
                font-size: 23px;
              `}
            > </h2>
            <h1 css={css`
        text-align: center;
        padding-top: 10px;
        padding-bottom: 20px;
        font-size: 13px;
      `}> 
             </h1>
            <div className='player-wrapper'>
                <ReactPlayer
                    css={css`
                    max-width: 600px;
                    height: 100%;
                    margin: 0px auto;
                    }
                  `}

                    className='react-player'
                    url={`https://www.youtube.com/watch?v=${YoutubeData1}`}    //  url
                    width='100%'         //   ()
                    height="400px"       //   ()
                    playing={false}        //   on
                    muted={false}          //   on
                    controls={true}       //    
                    light={false}         //  
                    pip={true}            // pip   
                    poster={''}   //    
                    //onEnded={() => handleVideo()}  //    
                />
                <br/>
                <ReactPlayer
                    css={css`
                    max-width: 600px;
                    margin: 0px auto;
                    }
                  `}
                    className='react-player'
                    url={`https://www.youtube.com/watch?v=${YoutubeData2}`}    //  url
                    width='100%'         //   ()
                    height='400px'        //   ()
                    playing={false}        //   on
                    muted={false}          //   on
                    controls={true}       //    
                    light={false}         //  
                    pip={true}            // pip   
                    poster={''}   //    
                    //onEnded={() => handleVideo()}  //    
                />
            </div>
        </div>
    )
}
export default YoutubeSlide;