/** @jsxImportSource @emotion/react */
import React from "react";
import { css } from "@emotion/react";
import { Chart } from "react-google-charts";
import "bootstrap/dist/css/bootstrap.min.css";
import { Container } from "react-bootstrap";

export default function GeoChart({
  Seoul_defCnt,
  Seoul_deathCnt,

  Busan_defCnt,
  Busan_deathCnt,

  Daegu_defCnt,
  Daegu_deathCnt,

  Incheon_defCnt,
  Incheon_deathCnt,

  Gwangju_defCnt,
  Gwangju_deathCnt,

  Daejeon_defCnt,
  Daejeon_deathCnt,

  Ulsan_defCnt,
  Ulsan_deathCnt,

  Gyeonggi_do_defCnt,
  Gyeonggi_do_deathCnt,

  Gangwon_do_defCnt,
  Gangwon_do_deathCnt,

  Chungcheongbuk_do_defCnt,
  Chungcheongbuk_do_deathCnt,

  Chungcheongnam_do_defCnt,
  Chungcheongnam_do_deathCnt,

  Jeollabuk_do_defCnt,
  Jeollabuk_do_deathCnt,
 
  Jeollanam_do_defCnt,
  Jeollanam_do_deathCnt,

  Gyeongsangbuk_do_defCnt,
  Gyeongsangbuk_do_deathCnt,
 

  Gyeongsangnam_do_defCnt,
  Gyeongsangnam_do_deathCnt,
 
  Jeju_defCnt,
  Jeju_deathCnt,

  CreateDt
}) {
  const options = {
    colorAxis: {
      minValue: 0,
      maxValue: Number(Gyeonggi_do_defCnt),
      colors: ["ffffff", "8B0000"],
    },
    region: "KR",
    resolution: "provinces",
  };

  console.log(Gangwon_do_deathCnt)

  const data = [
    ["City", "City", "", ""],
    ["KR-11", "", Number(Seoul_defCnt), Number(Seoul_deathCnt)],
    ["KR-26", "", Number(Busan_defCnt), Number(Busan_deathCnt)],
    ["KR-27", "", Number(Daegu_defCnt), Number(Daegu_deathCnt)],
    ["KR-28", "", Number(Incheon_defCnt), Number(Incheon_deathCnt)],
    ["KR-29", "", Number(Gwangju_defCnt), Number(Gwangju_deathCnt)],
    ["KR-30", "", Number(Daejeon_defCnt), Number(Daejeon_deathCnt)],
    ["KR-31", "", Number(Ulsan_defCnt), Number(Ulsan_deathCnt)],
    ["KR-41", "", Number(Gyeonggi_do_defCnt), Number(Gyeonggi_do_deathCnt)],
    ["KR-42", "", Number(Gangwon_do_defCnt), Number(Gangwon_do_deathCnt)],
    ["KR-43", "", Number(Chungcheongbuk_do_defCnt), Number(Chungcheongbuk_do_deathCnt)],
    ["KR-44", "", Number(Chungcheongnam_do_defCnt), Number(Chungcheongnam_do_deathCnt)],
    ["KR-45", "", Number(Jeollabuk_do_defCnt), Number(Jeollabuk_do_deathCnt)],
    ["KR-46", "", Number(Jeollanam_do_defCnt), Number(Jeollanam_do_deathCnt)],
    ["KR-47", "", Number(Gyeongsangbuk_do_defCnt), Number(Gyeongsangbuk_do_deathCnt)],
    ["KR-48", "", Number(Gyeongsangnam_do_defCnt), Number(Gyeongsangnam_do_deathCnt)],
    ["KR-49", "", Number(Jeju_defCnt), Number(Jeju_deathCnt)],
  ];

  return (
    <Container className="GeoChart">
       <h1
      css={css`
      text-align: center;
      padding-top: 10px;
      padding-bottom: 10px;
      font-size: 23px;
    `}
    className="GeoChart"
    >
        
      </h1>
      <h1
        css={css`
        text-align: center;
        padding-top: 0px;
        padding-bottom: 0px;
        font-size: 13px;
      `}
      >{CreateDt} </h1>

      <Chart
       css={css`
       max-width: 600px;
       margin: 10px auto;
       }
     `}
        chartType="GeoChart"
        width="100%"
        height="400px"
        data={data}
        options={options}
        
      />
      <h1
        css={css`
          text-align: center;
          font-size: 13px;
        `}
      >
         Google GeoChart     .
      </h1>
    </Container>
  );
}
